.progress-bar {
  width: 100%;
  height: 15px;
  overflow: hidden;
  position: relative;
  background-color: transparent;
  border: 1px solid #d9d9d9;
  &-background {
    transition: all 500ms ease-in-out;
    width: 0;
    position: absolute;
    background-color: #000;
    top: 0;
    left: 0;
    height: 100%;
  }
}
