.lookbook-preview {
  width: 100%;
  position: relative;
  &--image {
    width: 100%;
    height: 75vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    transition: all 500ms ease-in-out;
  }
  .button-preview-container {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba($color: #000000, $alpha: 0.5s);
    visibility: hidden;
    opacity: 0;
    transition: all 300ms ease-in-out;
    &.active {
      visibility: visible;
      opacity: 1;
    }
  }
}
