.main-form-page {
  width: 100%;
  background: #f5f5f5;
  background-size: cover;
  background-repeat: no-repeat;
  &-container {
    padding: 10rem 0;
    width: 100%;
    min-height: 100vh;
  }
}

.form-page-container {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  padding: 2rem 0;
}
