.preview {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba($color: #000000, $alpha: 0.9);
  z-index: 30;
  display: flex;
  visibility: hidden;
  opacity: 0;
  justify-content: center;
  align-items: center;
  transition: all 300ms ease-in-out;
  &.active {
    visibility: visible;
    opacity: 1;
  }
  &-image {
    max-width: 700px;
    min-width: 400px;
    z-index: 50;
    img {
      width: 100%;
      object-fit: cover;
    }
  }
}
