.lookbook-images {
  background-color: black;
  padding: 10rem 0;
  width: 100%;
  h1 {
    color: white;
    text-align: center;
    font-size: 5rem;
    position: fixed;
    z-index: 1;
    left: 50%;
    transform: translateX(-50%);
  }
  &-container {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    padding: 10rem 0;
  }
  .image-wrap {
    max-width: 500px;
    z-index: 10;
    min-width: 350px;
    transition: all 350ms ease-in-out;
    position: relative;
    &:hover {
      transform: scale(1.02);
      .image-wrap-info {
        opacity: 1;
        visibility: visible;
      }
    }
    &-info {
      background-color: rgba($color: #000, $alpha: 0.6);
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      z-index: 20;
      opacity: 0;
      visibility: hidden;
      transition: all 300ms ease-in-out;
      p {
        color: white;
      }
    }
  }
  img {
    width: 100%;
    object-fit: cover;
  }
}
