.carousel-container {
  width: 100%;
  .slider {
    max-width: 500px;
    height: 500px;
    margin: 0 auto;
  }
  .slide {
    img {
      width: 100%;
    }
    &-hidden {
      opacity: 0.5;
    }
    &-visible {
      opacity: 1;
    }
  }
}

.car {
  max-width: 750px;
  min-width: 600px;
  @media (max-width: 800px) {
    max-width: auto;
    min-width: auto;
    width: 100%;
  }
  position: relative;
  .carousel-actions {
    position: absolute;
    border: none;
    width: 30px;
    background: transparent;
    outline: none;
    top: 50%;
    transform: translateY(-50%);
    transition: all 300ms ease-in-out;
    opacity: 0.3;
    &:hover {
      opacity: 1;
    }
    &.back {
      left: 10px;
    }
    &.next {
      right: 10px;
    }
  }
  .dot-container {
    width: 100%;
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
  }
  .dot-item {
    border: none;
    background-color: white;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    border: 1px solid black;
    margin-left: 1rem;
    &.active {
      background-color: black;
      border: none;
    }
  }
  .slideeee {
    width: 100%;
    img {
      width: 100%;
      object-fit: cover;
      transition: all 300ms ease-in-out;
      animation: fadeInOut ease-in-out;
      animation-duration: 300ms;
    }
  }
}

.carousel-container {
  display: flex;
  width: fit-content;
  justify-content: flex-start;
  align-items: center;
  .carousel {
    max-width: 950px;
    max-height: 750px;
    justify-content: flex-start;
    align-items: center;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    &::-webkit-scrollbar {
      display: none;
    }
    .carousel-item {
      width: 100%;
      img {
        width: 100%;
      }
    }
  }

  .mini {
    &-item {
      max-width: 70px;
      opacity: 0.3;
      transition: all 300ms ease-in-out;
      &.visible {
        opacity: 1;
      }
      &:hover {
        opacity: 1;
      }
      img {
        width: 100%;
      }
    }
  }
}
