@font-face {
  font-family: "RobotoMono-Regular";
  src: url("./assets/fonts/RobotoMono-Regular.woff") format("woff"); /* Pretty Modern Browsers */
}
@font-face {
  font-family: "MonumentExtended-Ultrabold";
  src: url("./assets/fonts/MonumentExtended-Ultrabold.woff") format("woff"); /* Pretty Modern Browsers */
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%; //1 rem = 10px; 10px/16px = 62.5%
}

body {
  box-sizing: border-box;
  font-family: "RobotoMono-Regular", sans-serif;
}

a {
  text-decoration: none;
}
h1,
h2,
h3 {
  font-family: "MonumentExtended-Ultrabold", sans-serif;
  letter-spacing: 1px;
}

.container {
  max-width: 1366px;
  margin: 0 auto;
}

.button-header {
  display: inline-block;
  color: white;
  line-height: 14px;
  letter-spacing: 0.5px;
  font-size: 1.6rem;
  border: 2px solid white;
  padding: 1rem 3rem;
  transition: all 500ms ease-in-out;
  cursor: pointer;
  background: transparent;
  &:hover {
    background: white;
    color: black;
  }
}
