.button {
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.5px;
  text-align: center;
  max-width: 100%;
  padding: 10px 30px;
  cursor: pointer;
  background: #000;
  color: #fff;
  border: 1px solid #000;
  &:hover {
    background: #fff;
    color: #000;
    border: 1px solid #000;
  }

  &.reverse {
    background: #fff;
    color: #000;
    &:hover {
      background: #000;
      color: #fff;
    }
  }
}
