.resumen-item {
  padding: 1rem;
  min-width: 300px;
  min-height: 300px;
  background-color: white;
  color: black;
  display: none;
  h4 {
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 2rem;
  }
  &-values {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    div {
      font-size: 1.4rem;
      margin-bottom: 1.5rem;
    }
  }
  &-total {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
    padding: 2rem 0;
    border-top: 1px solid #d9d9d9;
    border-bottom: 1px solid #d9d9d9;
    h5 {
      font-size: 1.6rem;
    }
  }
}
