.checkout-resume {
  max-width: 1366px;
  margin: 0 auto;
  padding: 100px 0;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  .table-container {
    width: 100%;
    table {
      width: 100%;
    }
  }
  thead {
    border: solid 1px #000;
  }
  .table-item {
    padding: 10px 0;
    margin-bottom: 4em;
    font-size: 1.4rem;
    color: black;
    td {
      text-align: center;
    }
    &--desc {
      padding: 10px 0;
      text-align: left;
    }
    &--img {
      max-width: 100px;
      img {
        width: 100%;
      }
    }
  }
}
