.floating-nav {
  position: absolute;
  top: 500px;
  left: 10px;
  display: flex;
  flex-direction: row-reverse;
  transform-origin: top left;
  transform: rotate(-90deg);
  color: white;

  &.scroll {
    color: black;
  }

  &--item {
    font-size: 2rem;
    color: inherit;
    margin-left: 20px;
    position: relative;
    .line {
      background: white;
      height: 3px;
      position: absolute;
      left: 50%;
      bottom: -5px;
      transform: translateX(-50%);
      &.home {
        width: 30px;
      }
      .lookbook {
        width: 40px;
      }
      .store {
        width: 40px;
      }
      .contact {
        width: 40px;
      }
    }
  }
}
