.info-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  .radio {
    display: none;
  }
  &-group {
    padding: 5rem;
    background-color: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    margin: 0 auto;
    width: 100%;
    max-width: 700px;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    @media (max-width: 500px) {
      padding: 3rem;
    }
    h4 {
      font-size: 2rem;
      margin: 3rem 0;
      font-weight: 400;
      margin-bottom: 2rem;
    }
    h5 {
      font-size: 1.4rem;
      font-weight: 400;
      margin-bottom: 2rem;
    }
  }
  label {
    font-size: 1.2rem;
    margin-top: 2rem;
  }
  .label-radios {
    display: flex;
    align-items: center;
    justify-content: flex-start;
  }
  .checkbox-label {
    min-width: 15px;
    max-width: 15px;
    min-height: 15px;
    max-height: 15px;
    border: 1px solid black;
    margin: 0;
    margin-right: 1.5rem;
    background-color: white;
    &.active {
      background-color: black;
    }
  }
  .checkbox-input {
    display: none;
  }
  .multi-input {
    width: 100%;
    display: flex;
    justify-content: space-between;
    @media (max-width: 800px) {
      flex-direction: column;
    }
    &-item {
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      &.checkbox {
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        padding: 3rem 0 4rem 0;
      }
      &.m-b {
        margin-bottom: 3rem;
      }
      &.radios {
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        padding: 1rem 0;
      }
    }
    & .multi-input-item {
      width: 47%;
      @media (max-width: 800px) {
        width: 100%;
        label {
          margin-top: 1rem;
        }
      }
    }
  }
}

.finish {
  width: 100%;
  &-redirect {
    width: 100%;
    border: 1px solid #d9d9d9;
    margin-bottom: 4rem;
    &-ml {
      width: 90px;
      img {
        width: 100%;
      }
    }
    &-list {
      width: 180px;
      margin-right: 0.5rem;
      &-container {
        width: 50%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
      }
      img {
        width: 100%;
      }
    }
    &--heading {
      background-color: white;
      padding: 1rem 1rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &--body {
      display: flex;
      padding: 4rem 1rem;
      background-color: #f5f5f5;
    }
  }
  &-status {
    margin: 2rem 0;
    padding: 2rem;
    @media (max-width: 500px) {
      padding: 1rem;
    }
    border: 1px solid #d9d9d9;
    display: flex;
    flex-direction: column;
    &-item {
      display: flex;
      justify-content: space-between;
      margin-bottom: 1rem;
      span {
        font-size: 1.2rem;
      }
      &--type {
        font-weight: 700;
        &.fijo {
          width: 100px;
          @media (max-width: 500px) {
            display: none;
          }
        }
      }
      &--value {
        margin-right: auto;
        margin-left: 2rem;
      }
    }
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 1px solid black;
  -webkit-text-fill-color: black;
  -webkit-box-shadow: white;
  background-color: white;
  transition: background-color 5000s ease-in-out 0s;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0px 1000px white inset;
  background-color: transparent;
}

.mt0 {
  margin-top: 0 !important;
  @media (max-width: 800px) {
    margin-top: 1rem !important;
  }
}
