.filters {
  padding: 5rem 0;
  width: 100%;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  &-actions {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-top: 2rem;
    div {
      cursor: pointer;
      &:first-of-type {
        margin-right: 2rem;
      }
    }
  }
  &-options {
    width: 100%;
    position: absolute;
    top: 100px;
    left: 0;
    padding: 5rem;
    background-color: white;
    border: 1px solid black;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
    visibility: hidden;
    opacity: 0;
    &.active {
      opacity: 1;
      visibility: visible;
    }
    &-container {
      width: 100%;
      display: flex;
      align-items: flex-start;
      justify-content: space-around;
    }
    &-color,
    &-category,
    &-size {
      h4 {
        font-size: 1.8rem;
        margin-bottom: 1.5rem;
      }
      .filter-item {
        font-size: 1.4rem;
        margin-bottom: 1rem;
        cursor: pointer;
        width: 100%;
        position: relative;
        &.active::before {
          content: "";
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          left: -15px;
          width: 10px;
          height: 10px;
          border-radius: 50%;
          position: absolute;
          background-color: red;
        }
      }
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      text-align: left;
    }
  }
}
