.nav {
  width: 100%;
  position: fixed;
  left: 0;
  right: 0;
  padding: 2rem 0;
  background-color: transparent;
  transition: all 500ms ease-in-out;
  z-index: 1000;
  &-container {
    max-width: 1366px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 1rem;
    svg {
      fill: white;
      cursor: pointer;
      width: 25px;
      height: 25px;
      @media (max-width: 800px) {
        width: 20px;
        height: 20px;
      }
    }
  }
  .title {
    color: white;
    font-size: 3rem;
    text-align: center;
    transition: all 500ms ease-in-out;
    @media (max-width: 800px) {
      font-size: 2.4rem;
    }
  }
  h4 {
    color: white;
    font-size: 2rem;
    text-align: center;
  }
  &.scrolled {
    background-color: white;
    border-bottom: 1px solid #ddd;
    @media (max-width: 800px) {
      background-color: white;
      border-bottom: 1px solid #ddd;
    }
    svg {
      fill: black;
    }
    .title {
      color: black;
    }
  }
  &.dark {
    background-color: black;
    border: none;
    @media (max-width: 800px) {
      background-color: white;
      border-bottom: 1px solid #ddd;
    }
    .title {
      color: white;
    }
    svg {
      fill: white;
    }
  }
}

.backdrop {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 900;
  background: rgba($color: #000000, $alpha: 0.2);
  display: none;
  &.active {
    display: block;
  }
}
