.status-page {
  width: 100%;
  background: url("https://res.cloudinary.com/dvmll0ruo/image/upload/c_scale,w_2000/v1582160399/lookbook/7V9A3101_a23j4o.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  h1,
  h2,
  h4 {
    margin-bottom: 1rem;
  }
  &-container {
    padding: 5rem;
    background-color: white;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
