@keyframes sideAnimation {
  from {
    transform: translateY(-110vh);
  }
  to {
    transform: translateY(-20px);
  }
}
@keyframes sideAnimationDisable {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-110vh);
  }
}

.side-nav {
  position: absolute;
  width: 100%;
  height: 100vh;
  background: white;
  transition: all 1s ease-in-out;
  transform: translateY(-110vh);
  z-index: 100000;
  &-container {
    position: relative;
    max-width: 1366px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin: 0 auto;
    padding: 100px;
  }
  .category {
    display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    a {
      color: black;
      font-size: 2rem;
    }
    &.active {
      display: flex;
    }
  }
  svg {
    width: 30px;
    height: 30px;
    position: absolute;
    top: 30px;
    left: 0;
  }
  h2 {
    color: black;
    font-size: 4rem;
    cursor: pointer;
    margin-bottom: 2rem;
    @media (max-width: 800px) {
      font-size: 2.4rem;
    }
  }
  &.active {
    animation: sideAnimation 0.5s ease-in-out;
    animation-fill-mode: forwards;
    // animation-direction: normal;
  }
  &.disabled {
    animation: sideAnimationDisable 0.5s ease-in-out;
    animation-fill-mode: forwards;
  }
}
