.selector {
  width: 33%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  @media (max-width: 800px) {
    width: 100%;
    padding: 2rem;
  }
  h1 {
    font-size: 4rem;
    text-align: left;
    @media (max-width: 800px) {
      font-size: 3rem;
    }
  }
  h4 {
    font-size: 2.5rem;
    text-align: left;
    @media (max-width: 800px) {
      font-size: 2rem;
    }
  }
  h1,
  h4 {
    margin-bottom: 1.5rem;
  }
  .size-drop {
    position: relative;
    width: 100%;
    padding: 1rem 4.5rem 1rem 1rem;
    display: block;
    border: 1px solid #ddd;
    background-color: #fff;
    line-height: 1.45;
    font-size: 1.3rem;
    cursor: pointer;
    .arrow {
      width: 10px;
      top: 50%;
      transition: all 300ms ease-in-out;
      transform: translateY(-50%) rotate(90deg);
      position: absolute;
      right: 30px;
      &.active {
        transform: translateY(-50%) rotate(270deg);
      }
    }

    &__list {
      z-index: 2;
      background: #fff;
      position: absolute;
      display: none;
      visibility: hidden;
      top: 0;
      left: 0;
      top: 40px;
      max-height: 26rem;
      overflow-y: auto;
      width: 100%;
      border: 1px solid #ddd;
      border-top: 1px solid #000;
      &.active {
        display: block;
        visibility: visible;
      }
      &--item {
        width: 100%;
        padding: 10px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        cursor: pointer;
        &:hover {
          background: black;
          color: #fff;
        }
        &.disabled {
          cursor: not-allowed;
          color: #ddd;
        }
      }
    }
  }
  .info-container {
    margin-top: 3rem;
    p {
    }
  }
}
