.item {
  max-width: 32%;
  min-width: 250px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transition: all 300ms ease-in-out;
  margin-bottom: 50px;
  h4 {
    font-weight: 400;
  }
  &-image-container {
    width: 100%;
    background-color: #d9d9d9;
    img {
      width: 100%;
      object-fit: cover;
      transition: all 300ms ease-in-out;
      animation: fadeInOut ease-in-out;
      animation-duration: 300ms;
    }
  }
  .description {
    margin-top: 10px;
    text-align: center;
    font-size: 1.4rem;
  }
}
@keyframes fadeInOut {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
