.item-showcase {
  max-width: 1366px;
  margin: 0 auto;
  padding: 100px 0;
  background: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 100vh;
  @media (max-width: 800px) {
    padding: 5rem 0;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
