.overlay-cart {
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  display: none;
  @media (max-width: 800px) {
    display: none;
  }
  &.active {
    display: block;
  }
}
.close-cart {
  display: none;
  @media (max-width: 800px) {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    color: black;
    z-index: 10000;
    svg {
      width: 30px;
      height: 30px;
    }
  }
}
.cart {
  z-index: 10;
  min-width: 300px;
  min-width: 480px;
  @media (max-width: 800px) {
    min-width: auto;
    max-width: auto;
    width: 100%;
  }
  height: 100vh;
  padding: 50px 10px;
  padding-bottom: 40px;
  position: absolute;
  top: 0;
  right: 0;
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  // display: none;
  visibility: hidden;
  opacity: 0;
  border: solid 1px #ddd;
  // box-shadow: 0 6px 12px 3px rgba(210, 224, 233, 0.2);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.0975);
  background: white;
  z-index: 1000;
  transition: all 300ms ease-in-out;
  transform: translateX(500px);
  &.active {
    // display: block;
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
  }
  .empty {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    h2 {
      text-align: center;
      margin-bottom: 14px;
    }
  }
  &-item {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    &--info {
      margin-right: auto;
    }
    &--info,
    &--actions {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      flex-direction: column;
      padding-top: 20px;

      &__delete {
        cursor: pointer;
        color: #000;
        font-size: 1.2;
        &:hover {
          color: black;
        }
      }
      h4,
      p {
        font-size: 1.4rem;
        color: black;
      }

      p {
        color: #9d9d9d;
      }
    }
    &--img {
      width: 100%;
      max-width: 100px;
      img {
        width: 100%;
      }
    }
    &--actions {
      p {
        color: #000;
      }
    }
  }

  &-price-info {
    width: 100%;
    margin-top: auto;
    &__subtotal {
      padding: 25px 0;
      display: flex;
      justify-content: space-between;
    }
    h4 {
      font-size: 1.4rem;
      font-weight: bold;
      color: black;
    }
  }
}
